import axios from "axios";
const API = axios.create({baseURL: "/"})

API.interceptors.request.use((req)=>{
    if(localStorage.getItem("profile")){
        req.headers.Authorization = JSON.stringify(localStorage.getItem("profile").token);
    }
    return req
})

export const getUser = (userId) => API.get(`/user/${userId}`)
export const updateUser = (id, formData) => API.put(`/user/${id}`, formData)
export const getAllUser = () => API.get('/user')
export const getNotifications = (id) => API.get(`/user/${id}/notifications`,{userId : `${id}`})
export const getTimelineNotifications = (id) => API.get(`/user/${id}/timelinenotifications`)
export const followUser = (id, data) => API.put(`/user/${id}/follow`, data)
export const unFollowUser = (id, data) => API.put(`/user/${id}/unfollow`, data)
export const deleteUser = (id, data) => API.delete(`/user/${id}`, {data})